<template>
    <Header title="地址管理" />
    <van-address-list v-model="chosenAddressId" :list="list" default-tag-text="默认" @add="onAdd" @edit="onEdit" />
</template>
<script>
import Header from "@/components/Header"
import { reactive, toRefs, onMounted } from "vue"
import { useStore } from "vuex";
import { useRouter } from "vue-router";
export default {
    components: {
        Header
    },
    setup() {
        let store = useStore()
        let router = useRouter()
        const data = reactive({
            list: [],
            chosenAddressId: 1001
        })

        const init = () => {
            data.list = store.state.userAddress.map(item => {
                if (item.isDefault) {
                    data.chosenAddressId = item.id
                }
                return {
                    id: item.id,
                    name: item.name,
                    tel: item.tel,
                    address: `${item.province}${item.city}${item.county}`,
                    isDefault: item.isDefault
                }
            })

        }

        onMounted(() => {
            init()
        })

        const onAdd = () => {
            router.push({
                path: "/addressedit",
                query: {
                    type: "add"
                }
            })
        }
        const onEdit = (item, index) => {
            router.push({
                path: "/addressedit",
                query: {
                    id: item.id,
                    type: "edit"
                }
            })
        }

        return {
            ...toRefs(data),
            onAdd,
            onEdit,
        };
    },
}
</script>
<style scoped lang="less">
/deep/ .van-button--primary {
    background-color: #ffc400;
    border-color: #ffc400;
}
</style>